<li><code>GET</code></li>
<li><code>POST</code></li>
</ol>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for each correct name</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-2-1p" tabindex="-1"><a class="header-anchor" href="#question-2-1p" aria-hidden="true">#</a> Question 2 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Specify the status code that should be sent back in HTTP when:</p>
<ol>
<li>The code on the server crashes</li>
<li>The request was successfully carried out, and the requested resource is sent back in the body of the response</li>
<li>The URI in the request identifies a resource that doesn't exist</li>
</ol>
<p>You will get:</p>
<ul>
<li>0.33 points for each correct status code</li>
</ul>
</template>
<template v-slot:sample-answer>
<ol>
<li><code>500</code></li>
<li><code>200</code></li>
<li><code>404</code></li>
</ol>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for each correct status code</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-3-1p" tabindex="-1"><a class="header-anchor" href="#question-3-1p" aria-hidden="true">#</a> Question 3 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what the <code>Accept</code> header is used for in HTTP, give an example of a value it can have, and explain how that value should be interpreted.</p>
</template>
<template v-slot:sample-answer>
<p>The <code>Accept</code> header is used in HTTP requests to tell the server which data format the client wants the body of the response to be in. It can for example have the value <code>text/html</code>, which means that the client wants the body of the response to be in HTML.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for general description</li>
<li>0.33 points for sample value</li>
<li>0.33 points for description of sample value</li>
</ul>
</template>
</ExamQuestion>
