<h3 id="question-10-1p" tabindex="-1"><a class="header-anchor" href="#question-10-1p" aria-hidden="true">#</a> Question 10 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what <code>hover</code> is in CSS.</p>
</template>
<template v-slot:sample-answer>
<p><code>hover</code> is a pseudo class in CSS, which is a special class an element has when the element is in a certain state. An element gets the <code>hover</code> pseudo class when the user has the mouse cursor over the element.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for describing it as <em>pseudo class</em> or mentioning <em>certain state</em>, or similar</li>
<li>0.5 points for describing which <em>certain state</em> <code>hover</code> is (i.e. mouse over element)</li>
</ul>
</template>
</ExamQuestion>
