<h3 id="question-20-1p" tabindex="-1"><a class="header-anchor" href="#question-20-1p" aria-hidden="true">#</a> Question 20 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Is security improved by adding anti-forgery tokens to login forms and logout forms? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No, that would not increase security, since these forms can't be used to do any dangerous requests for the user.</p>
<p>For example, if a hacker manages to get a user's web browser to unwillingly send a logout request, the only consequence is that the user will be logged out. A bit inconvenient for the the user (she has to manually login again), but otherwise no harm done to the user.</p>
<p>And if a hacker manages to get a user's web browser to unwillingly send a login request, the most probable outcome is that the request will be denied, because the hacker doesn't know the user's username and password. And if the hacker would know these credentials, then she would probably login to the user's account from the hacker's own computer, which would be way easier than trying to exploit valid credentials in a CSRF attack.</p>
</template>
<template v-slot:marking-guidelines>
<p>1 point for an answer that has a valid justification (just saying something like <em>tokens always makes it more secure</em>, or explaining how tokens work, is not enough)</p>
</template>
</ExamQuestion>
<h3 id="question-21-3p" tabindex="-1"><a class="header-anchor" href="#question-21-3p" aria-hidden="true">#</a> Question 21 (3p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Can it be dangerous to open an email in a web browser that contains an image? Justify your answer, and be detailed.</p>
</template>
<template v-slot:sample-answer>
<p>Yes, that can be dangerous if a website incorrectly use GET requests to make changes on the server. Then the following can happen:</p>
<ol>
<li>The user logins on <code>bank.com</code>, at which point <code>bank.com</code> creates a new session for the user to remember which account she logged in to, and then sends back a cookie with the user's session id to the user's web browser, which the web browser then remembers</li>
<li>The user opens the email in her web browser, at which point the web browser sends an HTTP GET request (with the cookie (the session id)) to retrieve the image from the server</li>
<li>If the image URL is something like <code>bank.com/transfer?amount=3000to=hackers-account</code>, then the web browser would send this GET request to <code>bank.com</code>, and <code>bank.com</code> would transfer $3000 from the logged in bank account (according to the user's session) to the hacker's account</li>
</ol>
<p>That is, just by opening the email and load the image, the user could lose money on her bank account if her bank's website incorrectly use GET requests to make changes on the server. But if the bank correctly only use POST requests to make changes on the server, then opening an email with an image in a web browser won't be dangerous (at least can't the described vulnerability be exploited).</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>3 points for correct answer and valid justification</li>
<li>0 points for just saying <em>yes, the GET request does not have to be to an image, but can be a bad request</em> (have to explain how a GET request can be bad)</li>
</ul>
</template>
</ExamQuestion>
